import React from 'react';
const AuthorAvatar = ({ username, network }) => {
    function getFirstAlphabet(str) {
        if(str){
            var match = str.match(/[A-Za-z]/);
            return match ? match[0] : '';
        } else return ''
    }
    return <img className={`tb_nc_author_profile__`} src={`https://ui-avatars.com/api/?name=${getFirstAlphabet(username)}&background=000&color=fff&length=1`} alt={username} width={44} height={44} />
}

export default AuthorAvatar;