import React, { PureComponent } from "react";
import { GET_MEDIA_NAME } from "../../../../utils";
import AuthorAvatar from "./Elements/AuthorAvatar";


export default class Author extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      isAuthorImagevalid: true
    }
  }
  render() {
    const { author, authorClass, network } = this.props;

    return <div key={Math.random()} className={authorClass}>
      {!this.state.isAuthorImagevalid ?
        <AuthorAvatar authorClass={authorClass} username={author.name} network={network} color={network.id === null ? `#000` : network.color} />
        :
        <img className={`${authorClass}__`} src={author.picture && String(author.picture).includes('author.png') ? " " : author.picture?.replace(/[ ]+/g, "")} alt={GET_MEDIA_NAME(author.picture)} width={44} height={44}
          onError={(e) => {
            this.setState({
              isAuthorImagevalid: false
            })
          }}
        />
      }
    </div>
  }
}

